.textarea {
	touch-action: manipulation;
	display: inline-block;
	background: #fff;
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	min-height: 10em;
	border-radius: 8px;
	background-color: $color_cream;
	border: 1px solid $color_cream;
	outline: none;

	/*  trns($th, border-color, box-shadow); */
}

.textarea::-webkit-input-placeholder {
	color: #999;
	text-indent: 0;
}
.textarea::-moz-placeholder {
	opacity: 1;
	color: #999;
}
/*.textarea.textarea_focused {
	border-color: $color_active;
	box-shadow: 0 0 0 1px $color_active;
}*/
.textarea.textarea_width_available {
	width: 100%;
	resize: vertical;
}

.textarea.textarea_size_s {
	font-size: 13px;
	line-height: 16px;
	padding-left: 6px;
}
.textarea.textarea_size_m {
	font-size: 13px;
	line-height: 18px;
	padding: 3px 5px;
}
.textarea.textarea_size_l {
	font-size: 15px;
	line-height: 20px;
	padding: 5px 7px;
}
.textarea.textarea_size_xl {
	font-size: 18px;
	line-height: 22px;
	padding-left: 11px;
}
