  padding: 0;
  user-select: none;
  outline: 0;
  background: #23494a;
  border: 3px solid #fccd63;
  white-space: nowrap;
  /*	border-radius: 3px;*/
  text-decoration: none;
  transition: background 0.25s;
  transition: background 0.25s, border-color 0.25s;
  /* HOVER */
}

.button .button__text {
  font-family: "Roboto", sans-serif;
  font-weight: 900;
  position: relative;
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  vertical-align: top;
  color: #fccd63;
  /*text-transform: lowercase;*/
