.notice__inner {
  height: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
}

.notice__box {
  height: 100%;
  display: flex;
  /*	align-items: center;*/
  flex-wrap: wrap;
  position: relative;
}

.notice__section-sub {
  margin-bottom: 20px;
}

.notice__section-sub:last-child {
  margin-bottom: 0;
}

.notice__section {
  width: 100%;
  /*margin-bottom: 10px;
		&:last-child {
			margin-bottom: 0;
		}*/
}

.notice__title {
  font-size: 30px;
  line-height: 34px;
  color: #fccd63;
  margin-bottom: 10px;
}

.notice__text {
  font-size: 17px;
  line-height: 22px;
  color: #fff;
}

.notice__close {
  position: absolute;
  bottom: 15px;
  right: 15px;
  width: 50px;
  margin-left: auto;
  margin-right: 0;
}

.notice.notice_s__title {
  color: #19b7ff;
}

.form__inner {
  display: flex;
  flex-direction: column;
}

/* Навигация */

* {
  box-sizing: border-box;
}

html {
  min-height: 100%;
  min-width: 100%;
  overflow-y: scroll;
}

body {
  min-height: 100%;
  min-width: 100%;
  -webkit-overflow-scrolling: touch;
}

@media screen and (min-width: 768px) {
  .page__menu-mobile {
    display: none;
  }

  .layout__wrap {
    padding-left: 40px;
    padding-right: 40px;
  }

  .button_font_mono .button__text {
    letter-spacing: 0.15rem;
  }

  .button_size_s .button__text {
    margin: 3px 30px;
  }

  .button_size_m {
    font-size: 16px;
    line-height: 22 px;
  }

  .menu-main__controller {
    display: none;
  }

  .menu-main__list {
    display: flex;
  }

  .logo__img {
    max-width: 255px;
  }

  .banner__inner:after {
    bottom: 0px;
    right: auto;
    left: -40px;
    width: 185px;
    height: 257px;
    background: url("../img/deco/deco_flower.svg") no-repeat;
  }

  .banner__inner:before {
    position: absolute;
    content: "";
    bottom: 0px;
    right: -40px;
    width: 408px;
    height: 613px;
    background: url("../img/deco/deco_woman.svg") no-repeat;
  }

  .banner__title {
    font-size: 64px;
    line-height: 90px;
    max-width: 990px;
  }

  .banner__button {
    flex-direction: row;
  }

  .banner__button-join {
    margin-left: 100px;
    margin-right: 15px;
  }

  .banner__button-in {
    margin-left: 15px;
  }

  .markets__inner {
    padding-top: 70px;
    padding-bottom: 70px;
    flex-wrap: wrap;
    align-items: center;
  }

  .markets__about {
    width: calc(100% - 15px);
    padding-right: 15px;
  }

  .markets__info {
    width: calc(100% - 15px);
    padding-left: 15px;
  }

  .markets__deco:after {
    width: 444px;
    height: 340px;
    background: url("../img/deco/deco_feather.svg") no-repeat;
    top: 20px;
    bottom: auto;
    right: -35px;
  }

  .markets__img-phone {
    /*max-width: 100%;
			max-height: 100%;*/
    max-width: 100%;
    margin-right: 15px;
    margin-left: auto;
    margin-top: 30px;
  }

  .contact__inner {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .contact__box {
    max-width: 700px;
    border-radius: 32px;
    padding-left: 50px;
    padding-right: 50px;
  }

  .notice__section {
    width: 50%;
    padding-right: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .notice__title {
    font-size: 26px;
    line-height: 30px;
    margin-bottom: 5px;
  }

  .notice__text {
    font-size: 16px;
    line-height: 20px;
  }
}

@media screen and (min-width: 994px) {
  .header__row {
    position: relative;
  }

  .block-s__body {
    transform: translateX(0);
    position: relative;
  }

  .block-s_close .block-s__body {
    transform: translateX(0);
    animation: none;
  }

  .button_size_s .button__text {
    margin: 5px 45px;
  }

  .button_size_m .button__text {
    margin: 7px 15px;
  }

  .button_size_xl .button__text {
    margin: 12px 50px;
  }

  .menu-main__item-link {
    font-size: 26px;
    line-height: 28px;
  }

  .banner__title {
    margin-left: 50px;
  }

  .commissions__title {
    font-size: 64px;
  }

  .commissions__imgbox {
    width: 100%;
    display: flex;
    justify-content: center;
  }

  .commissions__textbox {
    display: none;
  }

  .markets__inner {
    flex-direction: row;
  }

  .markets__about {
    width: calc(50% - 15px);
  }

  .markets__link {
    font-size: 36px;
    line-height: 42px;
  }

  .markets__info {
    flex-direction: row;
    width: calc(50% - 15px);
  }

  .markets__img-phone {
    /*max-width: 100%;
			max-height: 100%;*/
    max-width: 100%;
    margin-right: auto;
    margin-left: 0;
    margin-top: 30px;
  }

  .contact__box {
    max-width: 880px;
    display: flex;
  }

  .contact__info {
    width: 50%;
  }

  .contact__info:after {
    content: "";
    position: absolute;
    width: 233px;
    height: 250px;
    background: url("../img/deco/deco_horse.svg") no-repeat;
    left: -50px;
    bottom: -70px;
  }

  .contact__body {
    width: 50%;
  }

  .notice__title {
    font-size: 30px;
    line-height: 34px;
    margin-bottom: 10px;
  }

  .notice__text {
    font-size: 17px;
    line-height: 22px;
  }
}

@media screen and (min-width: 1280px) {
  .layout__wrap {
    padding-left: 15px;
    padding-right: 15px;
  }

  .menu-main__item-link {
    font-size: 30px;
    line-height: 30px;
  }

  .banner__inner:after {
    left: -15px;
  }

  .banner__inner:before {
    right: -15px;
  }

  .markets__about {
    width: calc(55% - 15px);
  }

  .markets__info {
    width: calc(45% - 15px);
  }

  .contact__box {
    max-width: 1000px;
  }
}